import Main from '.././views/Main.vue';

export const loginRouter = {
    path: '/login',
    name: 'login',
    meta: {
        title: 'Login - '
    },
    component: (resolve) => require(['./../views/login.vue'], resolve)
};

export const page404 = {
    path: '/*',
    name: 'error-404',
    meta: {
        title: '404-'
    },
    component: (resolve) => require(['./../views/error-page/404.vue'], resolve)
};

export const page403 = {
    path: '/403',
    name: 'error-403',
    meta: {
        title: '403-'
    },
    component: (resolve) => require(['./../views/error-page/403.vue'], resolve)
};

export const page500 = {
    path: '/500',
    name: 'error-500',
    meta: {
        title: '500-'
    },
    component: (resolve) => require(['./../views/error-page/500.vue'], resolve)
};

// MainotherRouter
export const otherRouter = {
    path: '/',
    name: 'otherRouter',
    component: Main,
    children: [
        //{ path: 'home', title: {i18n: 'home'}, name: 'home_index', component: resolve => { require(['./views/home/home.vue'], resolve); } },
        { path: 'ownspace', title: '', name: 'ownspace_index', component: resolve => { require(['.././views/own-space/own-space.vue'], resolve); } },
        //{ path: 'order/:order_id', title: '', name: 'order_info', component: resolve => { require(['./views/advanced-router/component/order-info.vue'], resolve); } },  // 
        //{ path: 'shopping', title: '', name: 'shopping', component: resolve => { require(['./views/advanced-router/component/shopping-info.vue'], resolve); } },  // 
        { path: 'message', title: '', name: 'message_index', component: resolve => { require(['.././views/message/message.vue'], resolve); } }
    ]
};

// MainappRouter
export const appRouter = [
    {
        path: '/home',
        title: '',
        name: 'home',
        component: Main,
        children: [
            { path: 'home_index', title: '', name: 'home_index', component: resolve => { require(['.././views/home/home.vue'], resolve); } }
        ]
    },
    {
        path: '/device',
        title: '',
        name: 'device',
        component: Main,
        children: [
            { path: 'dev_register', title: '', name: 'dev_register', component: resolve => { require(['.././views/device/dev_register.vue'], resolve); } },
            { path: 'dev_list', title: '', name: 'dev_list', component: resolve => { require(['.././views/device/dev_list.vue'], resolve); }},
            { path: 'dev_log', title: '', name: 'dev_log', component: resolve => { require(['.././views/device/dev_log.vue'], resolve); }}
        ]
    },
    {
        path: '/config',
        title: '',
        name: 'config',
        component: Main,
        children: [
            { path: 'cfg_edit', title: '', name: 'cfg_edit', component: resolve => { require(['.././views/config/cfg_edit.vue'], resolve); } },
            //{ path: 'cfg_update', title: '', name: 'cfg_update', component: resolve => { require(['.././views/config/cfg_update.vue'], resolve); }},
            { path: 'create_model', title: '', name: 'create_model', component: resolve => { require(['.././views/config/create_model.vue'], resolve)}}
        ]
    },
    {
        path: '/task',
        title: '',
        name: 'task',
        component: Main,
        children: [
            { path: 'task_edit', title: '', name: 'task_edit', component: resolve => { require(['.././views/task/task_edit.vue'], resolve); } },
            { path: 'task_view', title: '', name: 'task_view', component: resolve => { require(['.././views/task/task_view.vue'], resolve); } },
        ]
    },
    {
        path: '/usermanage',
        title: '',
        name: 'usermanage',
        component: Main,
        children: [
            { path: 'group_mgmt', title: '', name: 'group_manage', component: resolve => { require(['.././views/user/group_manage.vue'], resolve); } },
            { path: 'user_mgmt', title: '', name: 'user_manage', component: resolve => { require(['.././views/user/user_manage.vue'], resolve); } }
        ]
    },
    {
        path: '/system',
        title: '',
        name: 'system',
        component: Main,
        children: [
            { path: 'sys_manage', title: '', name: 'sys_manage', component: resolve => { require(['.././views/system/sys_manage.vue'], resolve); } },
            { path: 'sys_log', title: '', name: 'sys_log', component: resolve => { require(['.././views/system/sys_log.vue'], resolve); } }
        ]
    }
];

// routers
export const routers = [
    loginRouter,
    otherRouter,
    ...appRouter,
    page500,
    page403,
    page404
];

//console.debug('router.js');


